.messageContainer {
    display: flex;
    align-items: flex-end;
    margin: 10px;
}

.avatarImage {
    height: 70px;
    width: 70px;
    border-radius: 50%;
    margin-top: 2vw;
}

.angle {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, rgba(5, 4, 4, 0.3) 0);
}

.text{
    padding: 5px;
    background-color: rgba(5, 4, 4, 0.3);
    width: 200px;
    height: 100px;
    border-radius: 5px 5px 5px 0;
    line-height: 8px;
}

.personName{    
    color: #ffe32e;
    padding-left: 10px;
}
.message {
    color: white;
    padding-left: 10px;
}

.time {
    color: white;
    float:right
}
