.header {
    background-color:wheat;
    height: 50px;
}

.nav {
    display: flex;
    align-items: center;
    transform: translateX(-460px);
    transition-duration: 700ms;
}

.nav:hover {
    transform: translateX(0px);
    transition-duration: 700ms;
}

.link {
    padding: 5px;
    margin: 10px;
    text-decoration: none;
    cursor: pointer;
}
.link:hover {
    color: rgb(90, 116, 233)
}

.activeLink {
    font-weight: bold;
    color:rgb(42, 65, 165)
}

.toggleNav {
    width: 20px;
    height: 20px;
    background-color: blue;   
}