.affair {
    width: 30vw;
    display: flex;
    justify-content: space-between;
    padding: 3px;
    font-size: 16px;
}

.priority {
    text-align:start
}

.low {
    color: green
}
.middle{
    color: #0f31f5
}
.high{
    color: red;
}

.deleteButton {
    background-color: white;
    color: blue;
    padding: 3px 8px;
    cursor: pointer;
}

.deleteButton:hover {
    background-color: blue;
    color: white;
}